* {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
  }
  .main {
    height: 100vh;
    width: 100vw;
  }
  .card1 {
    height: 100vh;
    background-color: black;
    width: 100%;
    text-align: center;
    padding: 1rem;
  }
  .card2 {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  
  h1 {
    color: yellow;
    font-size: 2.2rem;
    font-weight: bold;
    padding-top: 2rem;
  }
  input {
    height: 3rem;
    width: 16rem;
    border-radius: 0.8rem;
    border-color: blue;
    padding-left: 0.8rem;
  }
  .button1 {
    margin-left: 1rem;
    height: 2.4rem;
    width: 5rem;
    border-radius: 0.6rem;
    background-color: white;
    color: black;
  }
  .button1:hover {
    background-color: green;
    color: aliceblue;
  }
  ul {
    background-color: transparent;
    list-style: none;
    margin-top: 1rem;
  }
  .lists {
    background-color: blue;
    color: white;
    padding: 0px;
    height: 2.4rem;
    width: 25rem;
    margin-top: 1rem;
    border-radius: 1rem;
    justify-items: center;
  }
  li {
    padding-top: 0.4rem;
  }
  .btn1 {
    color: black;
    width: 7rem;
    height: 2.11rem;
    margin-top: 1rem;
    margin-left: 0.6rem;
    border-radius: 0.7rem;
  }
  .btn1:hover {
    color: white;
    background-color: red;
  }
  .check {
    display: flex;
    align-items: center;
  }
  .move {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  